import React, { FunctionComponent, useMemo, ReactElement, useState, useCallback } from 'react';
import { BodyShort, Heading, Search, Alert, Button, CopyButton } from '@navikt/ds-react';
import { isValidFodselsnummer } from '@navikt/ft-utils';
import { FlexColumn, FlexRow, VerticalSpacer } from '@navikt/ft-ui-komponenter';
import { Buldings3Icon, FigureInwardIcon, SilhouetteIcon, FigureOutwardIcon } from '@navikt/aksel-icons';
import { FormattedMessage, useIntl } from 'react-intl';
import styles from './brukerAvsenderPanel.module.css';
import Journalpost from '../../../typer/journalpostTsType';
import OppdaterMedBruker from '../../../typer/oppdaterBrukerTsType';
import Forh√•ndsvisBrukerRespons from '../../../typer/forh√•ndsvisBrukerResponsTsType';

const finnKj√∏nnBilde = (brukerFnr: string): ReactElement => {
  if (!brukerFnr || brukerFnr.length !== 11) {
    return <SilhouetteIcon className={styles.ikon} />;
  }
  const tall = parseInt(brukerFnr.charAt(8), 10);
  return tall % 2 === 0 ? <FigureOutwardIcon className={styles.ikon} /> : <FigureInwardIcon className={styles.ikon} />;
};

const finnAvsenderBilde = (journalpost: Journalpost): ReactElement => {
  const avsenderId = journalpost.avsender?.id;
  if (!avsenderId) {
    return <SilhouetteIcon className={styles.ikon} />;
  }
  if (avsenderId.length === 9) {
    return <Buldings3Icon className={styles.ikon} />;
  }
  if (avsenderId.length === 11) {
    return finnKj√∏nnBilde(journalpost.bruker.fnr);
  }
  return <SilhouetteIcon className={styles.ikon} />;
};

const lagBrukerAvsenderRad = (navn: string, id: string, ikon: ReactElement, title?: string): ReactElement => (
  <FlexColumn className={styles.kolBredde}>
    {title && (
      <FlexRow>
        <FlexColumn>
          <Heading size="small">
            <FormattedMessage id={title} />
          </Heading>
        </FlexColumn>
      </FlexRow>
    )}
    <VerticalSpacer sixteenPx />
    <FlexRow>
      <FlexColumn className={styles.ikonKol}>{ikon}</FlexColumn>
      <FlexColumn>
        <FlexRow>
          <FlexColumn>
            <BodyShort>{navn}</BodyShort>
          </FlexColumn>
        </FlexRow>
        <FlexRow>
          <FlexColumn className={styles.kopiTekst}>
            <BodyShort>{id}</BodyShort>
          </FlexColumn>
          <FlexColumn className={styles.clipBoard}>
            <CopyButton copyText={id} variant="action" />
          </FlexColumn>
        </FlexRow>
      </FlexColumn>
    </FlexRow>
  </FlexColumn>
);

type OwnProps = Readonly<{
  journalpost: Journalpost;
  hentForh√•ndsvisningAvS√∏ker: (fnr: string) => void;
  skalKunneEndreS√∏ker: boolean;
  brukerTilForh√•ndsvisning?: Forh√•ndsvisBrukerRespons;
  knyttS√∏kerTilJournalpost: (params: OppdaterMedBruker) => void;
  lasterBruker: boolean;
}>;

/**
 * BrukerAvsenderPanel - Inneholder detaljer om bruker og avsender
 */
const BrukerAvsenderPanel: FunctionComponent<OwnProps> = ({
  journalpost,
  hentForh√•ndsvisningAvS√∏ker,
  skalKunneEndreS√∏ker,
  brukerTilForh√•ndsvisning,
  knyttS√∏kerTilJournalpost,
  lasterBruker,
}) => {
  const intl = useIntl();
  const [s√∏kerFeilmelding, setS√∏kerFeilmelding] = useState<string | undefined>(undefined);
  const [fnrSomSkalForh√•ndsvises, setFnrSomSkalForh√•ndsvises] = useState<string | undefined>(undefined);
  const brukerBilde = useMemo(() => finnKj√∏nnBilde(journalpost.bruker?.fnr), [journalpost]);
  const avsenderBilde = useMemo(() => finnAvsenderBilde(journalpost), [journalpost]);

  const knyttS√∏kerTilJP = () => {
    if (fnrSomSkalForh√•ndsvises) {
      knyttS√∏kerTilJournalpost({
        journalpostId: journalpost.journalpostId,
        f√∏dselsnummer: fnrSomSkalForh√•ndsvises,
      });
    }
  };

  const forh√•ndsvisS√∏ker = (fnr: string | undefined) => {
    if (!fnr) {
      setS√∏kerFeilmelding(undefined);
    } else if (!isValidFodselsnummer(fnr)) {
      setS√∏kerFeilmelding(intl.formatMessage({ id: 'ValgtOppgave.S√∏k.BrukerFeil' }, { f√∏dselsnummer: fnr }));
    } else {
      setFnrSomSkalForh√•ndsvises(fnr);
      setS√∏kerFeilmelding(undefined);
      hentForh√•ndsvisningAvS√∏ker(fnr);
    }
  };

  const oppdaterMedBrukerKlikk = useCallback(
    (e: any) => {
      if (e.key === 'Enter') {
        const fnr = e.target?.value;
        forh√•ndsvisS√∏ker(fnr);
      }
    },
    [hentForh√•ndsvisningAvS√∏ker, s√∏kerFeilmelding],
  );

  return (
    <div className={styles.brukerAvsenderRad}>
      <FlexColumn>
        {skalKunneEndreS√∏ker && (
          <>
            <FlexRow>
              <FlexColumn>
                <Heading size="small">
                  <FormattedMessage id="ValgtOppgave.Bruker" />
                </Heading>
              </FlexColumn>
            </FlexRow>
            <VerticalSpacer sixteenPx />
            <Alert variant="warning">
              <BodyShort>
                <FormattedMessage id="ValgtOppgave.S√∏k.Bruker" />
              </BodyShort>
            </Alert>
            <VerticalSpacer sixteenPx />
            <div>
              <Search
                label={intl.formatMessage({ id: 'ValgtOppgave.S√∏k.FinnBruker' })}
                onKeyDown={oppdaterMedBrukerKlikk}
                onSearchClick={forh√•ndsvisS√∏ker}
                hideLabel={false}
              >
                <Search.Button type="button" loading={lasterBruker} />
              </Search>
              <VerticalSpacer eightPx />
              {s√∏kerFeilmelding && <BodyShort className={styles.error}>{s√∏kerFeilmelding}</BodyShort>}
              {brukerTilForh√•ndsvisning && (
                <>
                  {lagBrukerAvsenderRad(
                    brukerTilForh√•ndsvisning.navn,
                    brukerTilForh√•ndsvisning.f√∏dselsnummer,
                    finnKj√∏nnBilde(brukerTilForh√•ndsvisning.f√∏dselsnummer),
                  )}
                  <VerticalSpacer sixteenPx />
                  <Button type="button" onClick={knyttS√∏kerTilJP}>
                    <FormattedMessage id="ValgtOppgave.S√∏k.KnyttTil" />
                  </Button>
                </>
              )}
            </div>
          </>
        )}
        {journalpost.bruker?.navn && (
          <FlexRow>
            {lagBrukerAvsenderRad(journalpost.bruker.navn, journalpost.bruker.fnr, brukerBilde, 'ValgtOppgave.Bruker')}
          </FlexRow>
        )}
      </FlexColumn>
      {journalpost.avsender?.navn && (
        <>
          {lagBrukerAvsenderRad(
            journalpost.avsender.navn,
            journalpost.avsender.id,
            avsenderBilde,
            'ValgtOppgave.Avsender',
          )}
        </>
      )}
    </div>
  );
};
export default BrukerAvsenderPanel;
