import React, { FunctionComponent, useCallback } from 'react';
import { FormattedMessage } from 'react-intl';
import { useForm } from 'react-hook-form';
import { Alert, BodyLong, Button, Heading, Tag } from '@navikt/ds-react';

import { FlexColumn, FlexRow, VerticalSpacer } from '@navikt/ft-ui-komponenter';
import { Form } from '@navikt/ft-form-hooks';
import { NavAnsatt } from '@navikt/fp-types';
import VelgSakForm, { transformValues as transformValuesSak } from './innhold/VelgSakForm';
import Journalpost from '../../typer/journalpostTsType';
import Journalf√∏rSubmitValue, {
  DokumentTittelSubmitValue,
  OppdaterJournalf√∏rTittlerSubmitValue,
} from '../../typer/ferdigstillJournalf√∏ringSubmit';
import OppgaveOversikt from '../../typer/oppgaveOversiktTsType';
import SakDetaljer from './innhold/SakDetaljer';
import DokumentForm, {
  buildInitialValues as buildInitialValuesFlereDokumenter,
  transformValues as transformValuesFlereDokumenter,
} from './innhold/DokumentForm';
import BrukerAvsenderPanel from './innhold/BrukerAvsenderPanel';
import Journalf√∏ringFormValues from '../../typer/journalf√∏ringFormValues';
import OppdaterMedBruker from '../../typer/oppdaterBrukerTsType';
import JournalpostTittelForm from './innhold/JournalpostTittelForm';
import Forh√•ndsvisBrukerRespons from '../../typer/forh√•ndsvisBrukerResponsTsType';
import ReserverOppgaveType from '../../typer/reserverOppgaveType';

const dokumentTittelSkalStyresAvJournalpost = (jp: Journalpost): boolean => jp.dokumenter?.length === 1;

const buildInitialValues = (journalpost: Journalpost): Journalf√∏ringFormValues => {
  const docs = journalpost.dokumenter || [];
  return {
    saksnummerValg: undefined,
    ytelsetypeValg: undefined,
    journalpostTittel: journalpost.tittel,
    journalpostDokumenter: dokumentTittelSkalStyresAvJournalpost(journalpost)
      ? [{ dokumentId: docs[0].dokumentId, tittel: journalpost.tittel }]
      : buildInitialValuesFlereDokumenter(docs),
  };
};

const transformValuesEttDokument = (journalpost: Journalpost, tittel: string): DokumentTittelSubmitValue[] => {
  const dokumentId = journalpost.dokumenter?.at(0)?.dokumentId;
  if (!dokumentId) {
    throw new Error(`Finner ingen dokumenter p√• journalpost ${journalpost.journalpostId}`);
  }
  return [
    {
      dokumentId,
      tittel,
    },
  ];
};

const transformTittelValues = (
  values: Journalf√∏ringFormValues,
  journalpost: Journalpost,
): OppdaterJournalf√∏rTittlerSubmitValue | undefined => {
  const nyJournalpostTittel = journalpost.tittel !== values.journalpostTittel ? values.journalpostTittel : undefined;
  const endredeDokumenter =
    nyJournalpostTittel && dokumentTittelSkalStyresAvJournalpost(journalpost)
      ? transformValuesEttDokument(journalpost, nyJournalpostTittel)
      : transformValuesFlereDokumenter(values, journalpost.dokumenter || []);
  if (nyJournalpostTittel || endredeDokumenter.length > 0) {
    return {
      journalpostTittel: nyJournalpostTittel,
      dokumenter: endredeDokumenter,
    };
  }
  return undefined;
};

const transformValues = (
  values: Journalf√∏ringFormValues,
  journalpost: Journalpost,
  oppgave: OppgaveOversikt,
): Journalf√∏rSubmitValue => {
  if (!oppgave.enhetId) {
    throw Error('Kan ikke journalf√∏re uten at enhet er satt');
  }
  return {
    journalpostId: journalpost.journalpostId,
    enhetId: oppgave.enhetId,
    oppgaveId: oppgave.id,
    oppdaterTitlerDto: transformTittelValues(values, journalpost),
    ...transformValuesSak(values, journalpost),
  };
};

type OwnProps = Readonly<{
  journalpost: Journalpost;
  oppgave: OppgaveOversikt;
  avbrytVisningAvJournalpost: () => void;
  submitJournalf√∏ring: (params: Journalf√∏rSubmitValue) => void;
  knyttJournalpostTilBruker: (params: OppdaterMedBruker) => void;
  forh√•ndsvisBruker: (fnr: string) => void;
  brukerTilForh√•ndsvisning?: Forh√•ndsvisBrukerRespons;
  lasterBruker: boolean;
  navAnsatt: NavAnsatt;
  reserverOppgave: (data: ReserverOppgaveType) => void;
  oppdaterValgtOppgave: (oppgave: OppgaveOversikt) => void;
}>;

/**
 * JournalpostDetaljer - Viser detaljer om valgt journalpost
 */
const JournalpostDetaljer: FunctionComponent<OwnProps> = ({
  journalpost,
  oppgave,
  avbrytVisningAvJournalpost,
  submitJournalf√∏ring,
  knyttJournalpostTilBruker,
  forh√•ndsvisBruker,
  brukerTilForh√•ndsvisning,
  lasterBruker,
  reserverOppgave,
  navAnsatt,
  oppdaterValgtOppgave,
}) => {
  const skalKunneEndreS√∏ker = !journalpost.bruker;
  const saker = journalpost.fagsaker || [];
  const formMethods = useForm<Journalf√∏ringFormValues>({
    defaultValues: buildInitialValues(journalpost),
  });
  const submitJournal = useCallback((values: Journalf√∏ringFormValues) => {
    submitJournalf√∏ring(transformValues(values, journalpost, oppgave));
  }, []);

  const isSubmittable = formMethods.formState.isDirty;

  const reserverOppgaveAction = useCallback(() => {
    const reservasjonFor = !oppgave.reservertAv ? navAnsatt.brukernavn : '';
    reserverOppgave({
      oppgaveId: oppgave.id.toString(),
      versjon: oppgave.versjon,
      reserverFor: reservasjonFor,
    });
    oppdaterValgtOppgave({ ...oppgave, reservertAv: reservasjonFor, versjon: oppgave.versjon + 1 });
  }, [reserverOppgave]);

  return (
    <Form<Journalf√∏ringFormValues> formMethods={formMethods} onSubmit={submitJournal}>
      <JournalpostTittelForm journalpost={journalpost} />
      <VerticalSpacer eightPx />
      {oppgave.reservertAv && navAnsatt.brukernavn === oppgave.reservertAv && (
        <FlexRow>
          <FlexColumn>
            <BodyLong>
              <FormattedMessage id="Oppgavetabell.SakenErTattAv" />
              <Tag size="small" variant="info-moderate" style={{ marginLeft: '0.5rem' }}>
                <FormattedMessage id="Oppgavetabell.Meg" />
              </Tag>
              <Button variant="tertiary" size="small" onClick={reserverOppgaveAction} style={{ marginLeft: '0.5rem' }}>
                <FormattedMessage id="Oppgavetabell.FjernMeg" />
              </Button>
            </BodyLong>
          </FlexColumn>
        </FlexRow>
      )}
      {oppgave.reservertAv && navAnsatt.brukernavn !== oppgave.reservertAv && (
        <FlexRow>
          <FlexColumn>
            <BodyLong>
              <FormattedMessage id="Oppgavetabell.SakenErTattAv" />
              <Tag size="small" variant="neutral-moderate" style={{ marginLeft: '0.5rem' }}>
                {oppgave.reservertAv}
              </Tag>
            </BodyLong>
          </FlexColumn>
        </FlexRow>
      )}
      {!oppgave.reservertAv && (
        <FlexRow>
          <FlexColumn>
            <BodyLong>
              <Button variant="tertiary" size="small" onClick={reserverOppgaveAction}>
                <FormattedMessage id="Oppgavetabell.SettP√•Meg" />
              </Button>
            </BodyLong>
          </FlexColumn>
        </FlexRow>
      )}
      <VerticalSpacer sixteenPx />
      <BrukerAvsenderPanel
        journalpost={journalpost}
        hentForh√•ndsvisningAvS√∏ker={forh√•ndsvisBruker}
        skalKunneEndreS√∏ker={skalKunneEndreS√∏ker}
        lasterBruker={lasterBruker}
        brukerTilForh√•ndsvisning={brukerTilForh√•ndsvisning}
        knyttS√∏kerTilJournalpost={knyttJournalpostTilBruker}
      />
      <VerticalSpacer twentyPx />
      {oppgave.beskrivelse && (
        <>
          <FlexRow>
            <FlexColumn>
              <Heading size="small">
                <FormattedMessage id="ValgtOppgave.Notat" />
              </Heading>
            </FlexColumn>
          </FlexRow>
          <FlexRow>
            <FlexColumn>
              <BodyLong>{oppgave.beskrivelse}</BodyLong>
            </FlexColumn>
          </FlexRow>
          <VerticalSpacer twentyPx />
        </>
      )}
      <VerticalSpacer sixteenPx />
      <FlexRow>
        <FlexColumn>
          <Heading size="small">
            <FormattedMessage id="ValgtOppgave.Dokumenter" />
          </Heading>
        </FlexColumn>
      </FlexRow>
      {journalpost.dokumenter && (
        <>
          <VerticalSpacer eightPx />
          <DokumentForm
            journalpost={journalpost}
            dokumentTittelStyresAvJournalpostTittel={dokumentTittelSkalStyresAvJournalpost(journalpost)}
          />
          <VerticalSpacer thirtyTwoPx />
        </>
      )}
      <FlexRow>
        <FlexColumn>
          <Heading size="small">
            <FormattedMessage id="ValgtOppgave.RelaterteSaker" />
          </Heading>
        </FlexColumn>
      </FlexRow>
      <VerticalSpacer eightPx />
      {skalKunneEndreS√∏ker && (
        <Alert variant="info">
          <FormattedMessage id="ValgtOppgave.RelaterteSaker.ManglerS√∏ker" />
        </Alert>
      )}
      {saker.map(sak => (
        <SakDetaljer sak={sak} key={sak.saksnummer} />
      ))}
      <VerticalSpacer thirtyTwoPx />
      <FlexRow>
        <FlexColumn>
          <Heading size="small">
            <FormattedMessage id="ValgtOppgave.KnyttTilSak" />
          </Heading>
        </FlexColumn>
      </FlexRow>
      <VerticalSpacer eightPx />
      <VelgSakForm
        isSubmittable={isSubmittable}
        journalpost={journalpost}
        avbrytVisningAvJournalpost={avbrytVisningAvJournalpost}
        erKlarForJournalf√∏ring={!skalKunneEndreS√∏ker}
      />
    </Form>
  );
};
export default JournalpostDetaljer;
